<template>
  <div>
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-1">
        <b-avatar class="mr-1" size="40" variant="light-success">
          <book-icon :style="{ color: 'var(--success)' }" size="30" />
        </b-avatar>
        <h4 class="m-0">    </h4>
      </div>
      <span>
				               .
			</span>
    </div>
    <div class="p-0">
      <app-collapse style="width: 100%">
        <app-collapse-item style="padding: 0 !important" v-for="(value, key) in floors" :title="key" class="mt-1">
          <div v-for="unit in value" class="mb-2">
            <p class="unit-title p-0">{{unit.unit_name}}</p>
            <b-form
                class="meterage-form"
                @submit.prevent
            >
              <div class="d-flex">
                <b-form-group>
                  <div class="d-flex">
                    <label class="my-auto mr-1"></label>
                    <b-form-input
                        v-model="unit.unit_data.footage"
                        id="renter-name"
                        name="renter-name"
                        style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
                    />
                  </div>
                </b-form-group>

                <b-form-group>
                  <div class="d-flex">
                    <label class="my-auto" style="width: 50%;margin-right: 25px"> </label>
                    <b-form-input
                        v-model="unit.unit_data.resident_count"
                        id="renter-name"
                        name="renter-name"
                        style="text-align: left; font-weight: 300; letter-spacing: 0.1em; font-size: 120%; direction: ltr; padding-top: 10px;"
                    />
                  </div>
                </b-form-group>
              </div>
            </b-form>
          </div>
          <b-button @click="registerMeterage(value)" variant="primary" block></b-button>
        </app-collapse-item>
      </app-collapse>
    </div>

  </div>
</template>

<script>
import { ValidationObserver, ValidationProvider } from 'vee-validate'
import AppCollapse from '@core/components/app-collapse/AppCollapse.vue'
import AppCollapseItem from '@core/components/app-collapse/AppCollapseItem.vue'
import {
  BAvatar,
  BButton,
  BCard,
  BCardBody,
  BCardTitle,
  BCol,
  BForm,
  BFormGroup,
  BFormInput,
  BImg,
  BInputGroup,
  BModal,
  BRow,
} from 'bootstrap-vue'
import axios from "@/libs/axios";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import BookIcon from "@core/components/svg-icons/BookIcon.vue";
export default {
  name: "meterage",
  components: {
    BAvatar,
    BookIcon,
    BFormGroup,
    BFormInput,
    BInputGroup,
    BForm,
    AppCollapse,
    AppCollapseItem,
    BRow,
    BCol,
    BCard,
    BCardBody,
    BCardTitle,
    BImg,
    BButton,
    BModal,
    ValidationProvider,
    ValidationObserver,
  },
  data() {
    return {
      token: '',
      floors: {},
    }
  },
  mounted() {
    this.token =  localStorage.getItem('token')
    axios
        .get('//sm.oop.ir/api/v1/manager/unit', {
          headers: {
            'Authorization': 'Bearer ' + this.token
          }
        })
        .then(response => {
          response.data.list.forEach(unit=> unit['unit_data'] = {unit_id: unit.unit_id,footage: unit.footage,resident_count: unit.resident_count})
          this.floors = response.data.list.reduce((groups, item) => {
            const group = (groups[item.floor.floor_name] || []);
            group.push(item);
            groups[item.floor.floor_name] = group;
            return groups;
          }, {})
        })
        .finally(() => {
            }
        )
  },
  methods: {
    registerMeterage(floor) {
      let units = []
      floor.forEach(unit => {
        units.push(unit.unit_data)
      })
      axios
          .post('//sm.oop.ir/api/v1/manager/unit/store',
              {
                unit:units
              },
              {
                headers: {
                  'Authorization': 'Bearer ' + this.token
                }
              })
          .then(response => {
              if (this.$route.query.redirect && this.$route.query.type) {
                  this.$router.push({ path: `/formula?type=${this.$route.query.type}&redirect=${this.$route.query.redirect}`})
              }
            this.$toast({
              component: ToastificationContent,
              props: {
                title: '     ',
                icon: 'EditIcon',
                variant: 'success',
              },
            })
          })
          .finally(() => {
              }
          )
    }
  }
}
</script>

<style scoped>
.unit-title {
    text-align: left;
    font-weight: 400;
    font-size: 20px;
    line-height: 29px;
    color: #63587C;
}
</style>
